    let capture =  document.querySelector("[contenteditable]");
    capture.addEventListener("paste", function(e){
      let captureImg = document.querySelector("#tsOutputImage");
      // e.clipboardData.types.length == 0  e.clipboardData.types[0] == "Files"
      if (!e.clipboardData || !e.clipboardData.types || (e.clipboardData.types.length != 1) || (e.clipboardData.types[0] != "Files")) return true;
      // (getAsString)
      let imageFile = e.clipboardData.items[0].getAsFile();
      // FileReader
      let fr = new FileReader();
      fr.onload = function(e) {
        // onloade.target.resultbase64
        let base64 = e.target.result;
        captureImg.src = base64;
        capture.childNodes.forEach(function(child){
          child.height = "100"
        });

        //let imgRect = capture.childNodes[0].getBoundingClientRect(); // 
        let nh = capture.childNodes[0].naturalHeight;
        let nw = capture.childNodes[0].naturalWidth;
        //let sx = 65;
        //let sy1 = 640;
        //let sy2 = 640+75;
        //let dx = 410;
        //let dy = 55;
        let sx = Math.round(nw/2 - (400/2)*(nh/983));
        let dx = Math.round(400 * nh/983);
        let sy1 = Math.round(nh * 640/983);
        let sy2 = Math.round(nh * (640+75)/983);
        let dy = Math.round(nh * 55/983);

        let captureCanvas1 = document.getElementById('tsOutputCanvas1');
        captureCanvas1.style.height = dy + "px";
        captureCanvas1.style.width = dx + "px";
        let captureContext1 = captureCanvas1.getContext('2d');
        captureContext1.drawImage(captureImg, sx, sy1, dx, dy, 0, 0, 410, 55);

        let buf = document.querySelector('#tsOutputCanvas1');
        Tesseract.recognize(buf, 'jpn',
            { logger: function(m) { document.querySelector('#tsOcrProgressManu').textContent = "log:1-" + m.status; } }
        )
        .then(function(result){
            let buf = result.data.text;
            buf = buf.replace(/\s+/g, ''); // 
            _this.tsSearchWordCls = 1;
            _this.tsSearchWord = buf;
        });
        
        let captureCanvas2 = document.getElementById('tsOutputCanvas2');
        captureCanvas2.style.height = dy + "px";
        captureCanvas2.style.width = dx + "px";
        let captureContext2 = captureCanvas2.getContext('2d');
        captureContext2.drawImage(captureImg, sx, sy2, dx, dy, 0, 0, 410, 55);

        buf = document.querySelector('#tsOutputCanvas2');
        Tesseract.recognize(buf, 'jpn',
            { logger: function(m) { document.querySelector('#tsOcrProgressManu').textContent = "log:1-" + m.status; } }
        )
        .then(function(result){
            let buf = result.data.text;
            buf = buf.replace(/\s+/g, ''); // 
            _this.tsSearchWordCls = 1;
            _this.tsSearchWord2 = buf;
        });
      };
      fr.readAsDataURL(imageFile);
    });


