        else if(lastWord == "") newWord = "";
        if(newWord != "") _this.tsSearchWord = _this.tsSearchWord.slice(0, _this.tsSearchWord.length-1) + newWord;

      } else if (word == "" && _this.tsSearchWord != "") {
        let lastWord = _this.tsSearchWord.slice(-1);
        let newWord = "";
        if(lastWord == "") newWord = "";
        else if(lastWord == "") newWord = "";
        else if(lastWord == "") newWord = "";
