    mFsDblClickSupTouch(damage, supporter) { // ()
      let _this = this;

      // 
      // 
      if (_this.fsTouchDamageId == damage.id && _this.fsTouchSupporterName == supporter.name) {
        _this.mFsDblClickSup(damage, supporter);
      } else {
        _this.fsTouchDamageId = damage.id;
        _this.fsTouchSupporterName = supporter.name;
        setTimeout(function(){
          _this.fsTouchDamageId = null;
          _this.fsTouchSupporterName = null;
        }, 500); // 500ms
      }
    },

    mFsDblClickSup(damage, supporter) { // ()
      supporter.order = 999; // 
      supporter.supporterName = '';

      // 
      damage.supporters.sort(function(first, second){
        if (first.order > second.order){
          return 1;
        }else if (first.order < second.order){
          return -1;
        }else{
          return 0;
        }
      });

      // 1
      let cnt = 1;
      damage.supporters.forEach(function(supporter) {
        supporter.order = cnt;
        cnt++;
      });
      
      this.mFsCalcDamage(damage);
    },

    mFsChangeAttacker(damage) { // 
      let _this = this;

      // 
      damage.attackList.splice(0) // 
      let flgSkill1 = false;
      let flgSkill2 = false;
      let flgBurst = false;
      _this.nikkes.forEach(function(nikke){
        if (nikke.name == damage.attackerName) {
          nikke.skill1Infos.forEach(function(info){
            if (!flgSkill1 && info.effect == "()") {
              damage.attackList.push("1");
              flgSkill1 = true;
            };
          });
          nikke.skill2Infos.forEach(function(info){
            if (!flgSkill2 && info.effect == "()") {
              damage.attackList.push("2");
              flgSkill2 = true;
            };
          });
          nikke.burstInfos.forEach(function(info){
            if (!flgBurst && (info.effect == "()" || info.effect == "()")) {
              damage.attackList.push("");
              flgBurst = true;
            };
          });
        }
      });
      damage.attackCls = ""

      damage.attackerSkillLv = [0, 10, 10, 10];

      // 
      damage.attackerSkills.splice(0) // 
      let newSkill = null;
      let newLv = null;
      _this.nikkes.forEach(function(nikke){
        if(nikke.name == damage.attackerName){
          newSkill = JSON.parse(JSON.stringify(_this.fsUseSkillProt));
          newSkill.id = 0;
          newSkill.note = "1" + nikke.skill1Ct;
          newSkill.titleFlg = true;
          newSkill.skillCls = 1;
          for (let i = 0; i < 10; ++i) {
            newLv = JSON.parse(JSON.stringify(_this.fsLevelProt));
            newLv.value = "";
            newSkill.lvs.push(newLv);
          };
          newSkill.useSkillFlg = 0;
          damage.attackerSkills.push(newSkill);

          nikke.skill1Infos.forEach(function(info){
            newSkill = JSON.parse(JSON.stringify(_this.fsUseSkillProt));
            newSkill.id = damage.attackerSkills.length;
            newSkill.note = info.note;
            newSkill.titleFlg = false;
            newSkill.skillCls = 1;
            newSkill.skillTarget = info.skillTarget;
            newSkill.effect = info.effect;
