          if(!_this.qfFil_S_OH_BUSXX && !reg.test(nikke.burst)) disp = 'none';
        }

        if((_this.qfFil_S_UP_ATK
            || _this.qfFil_S_UP_CRIDMG
            || _this.qfFil_S_UP_CRIPER
            || _this.qfFil_S_UP_PATDMG
            || _this.qfFil_S_UP_CODDMG
