      keywrodHira = keywrodHira.replace(//g, '');
      keywrodHira = keywrodHira.replace(//g, '');
      keywrodHira = keywrodHira.replace(//g, '');
      keywrodHira = keywrodHira.replace(//g, '');
      keywrodHira = keywrodHira.replace(//g, '');

      //if(_this.tsSearchWordCls == 1) {
        keywrod1 = keywrod1.replace(/!/g, '');
        keywrod1 = keywrod1.replace(/\?/g, '');
        keywrod2 = keywrod2.replace(/!/g, '');
        keywrod2 = keywrod2.replace(/\?/g, '');
      //};

      keywrod1 = keywrod1.replace(//g, '1');
      keywrod1 = keywrod1.replace(//g, '2');
      keywrod1 = keywrod1.replace(//g, '3');
      keywrod1 = keywrod1.replace(//g, '4');
      keywrod1 = keywrod1.replace(//g, '5');
      keywrod1 = keywrod1.replace(//g, '6');
      keywrod1 = keywrod1.replace(//g, '7');
      keywrod1 = keywrod1.replace(//g, '8');
      keywrod1 = keywrod1.replace(//g, '9');
      keywrod1 = keywrod1.replace(//g, '0');

      keywrod2 = keywrod2.replace(//g, '1');
      keywrod2 = keywrod2.replace(//g, '2');
      keywrod2 = keywrod2.replace(//g, '3');
      keywrod2 = keywrod2.replace(//g, '4');
      keywrod2 = keywrod2.replace(//g, '5');
      keywrod2 = keywrod2.replace(//g, '6');
      keywrod2 = keywrod2.replace(//g, '7');
      keywrod2 = keywrod2.replace(//g, '8');
      keywrod2 = keywrod2.replace(//g, '9');
      keywrod2 = keywrod2.replace(//g, '0');

      // 
      if (!_this.tsTalkNikkes.some(item => item.check === true)) forceCheck = true;

      // On
      let hitMax = 0;
      if(_this.tsImageManu == 1 || _this.tsImageAuto == 1) {
        let cmp1 = _this.trigramify(keywrod1);
        let cmp2 = _this.trigramify(keywrod2);
        if(keywrod1 != "") {
          data.forEach(function(nikke) {
            _this.tsTalkNikkes.forEach(function(target){
              if((target.name == nikke.name && target.check) || forceCheck) { // 
                nikke.cases[0].hit = _this.relevance(nikke.cases[0].trigramify, cmp1); // 
                if(nikke.cases[0].hit > hitMax) hitMax = nikke.cases[0].hit;
                nikke.cases[1].hit = _this.relevance(nikke.cases[1].trigramify, cmp2); // 
                if(nikke.cases[1].hit > hitMax) hitMax = nikke.cases[1].hit;
              };
            });
          });
        };
      };

      if(hitMax < 0.3 && _this.tsSearchWordCls == 1) {
        // 
        data = data.filter(function (nikke) {
          return nikke.disp; // 
        });
      } else {
        data = data.filter(function (nikke) {
          let flgFing = false;
          nikke.disp = false;
          if(keywrod1 != "") {
            _this.tsTalkNikkes.forEach(function(target){
              if((target.name == nikke.name && target.check) || forceCheck) { // 
                nikke.cases.forEach(function(caseItem){ //12
                  if(_this.tsSearchType == "") {
                    if(caseItem.select.indexOf(keywrod1) >= 0) {
                      nikke.disp = true;
                      flgFing = true;
                    } else if(_this.tsHiragana && caseItem.hiragana.indexOf(keywrodHira) >= 0) {
                      nikke.disp = true;
                      flgFing = true;
                    } else if(_this.tsImageManu == 1 || _this.tsImageAuto == 1) {
                      if(hitMax >= 0.3 && caseItem.hit >= (hitMax - 0.1)) {
                        nikke.disp = true;
                        flgFing = true;
                      };
                    };
                  } else {
                    if(caseItem.select.startsWith(keywrod1)) {
                      nikke.disp = true;
                      flgFing = true;
                    } else if(_this.tsHiragana && caseItem.hiragana.startsWith(keywrodHira)) {
                      nikke.disp = true;
                      flgFing = true;
                    } else if(_this.tsImageManu == 1 || _this.tsImageAuto) {
                      if(hitMax >= 0.3 && caseItem.hit >= (hitMax - 0.1)) {
                        nikke.disp = true;
                        flgFing = true;
                      };
                    };
                  };
                });            
              };
            });
          };
          return flgFing;
        });
      };

      return data
    }
  },

  destroyed: function () { // PT
    window.removeEventListener('dragstart', function(e){}, false)
    window.removeEventListener('drag', function(e){}, false)
    window.removeEventListener('dragover', function(e){}, false)
    window.removeEventListener('drop', function(e){}, false)
    //clearInterval(this.updateMessageTimer)
  }

}
</script>


